	module.exports = {"data":{"markdownRemark":{"html":"<h1><code>instanceof</code> operator</h1>\n<p>The <code>instanceof</code> operator allows you to check the validity of a <code>IS A</code> relationship. If at any point of time, we are not sure about this and we want to validate this at runtime, we can do the following:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-java\"><code><span class=\"token comment\" spellcheck=\"true\">//assuming vehicle is an instance of Class `Car` the expression inside the 'if' will  return true</span>\n<span class=\"token keyword\">if</span><span class=\"token punctuation\">(</span>vehicle <span class=\"token keyword\">instanceof</span> <span class=\"token class-name\">Car</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n    <span class=\"token comment\" spellcheck=\"true\">//do something if vehicle is a Car</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>","fields":{"slug":"/java/instanceof-operator/"},"frontmatter":{"title":"instanceof Operator"}}},"pathContext":{"slug":"/java/instanceof-operator/"}}

